/* global Omplayer */

import Raven from 'raven-js'
import { dsn } from './lib/sentry'

if (process.env.NODE_ENV !== 'production') {
  console.log(`当前运行环境: ${process.env.NODE_ENV}`)
} else {
  // sentry 监控
  Raven.config(dsn, {
    ignoreUrls: [/brace\.video\.qq\.com/, /vm\.gtimg\.cn/],
    release: process.env.RAVEN_VERSION
  }).install()
}

// 载入样式
require('./assets/typo.css')
require('./assets/debug.less')

// 加载相关组件
import $ from 'myzepto'
import querystring from 'querystring'

// 啄木鸟上报
import initZmn from './lib/zmn'
initZmn({ zepto: $, appname: 'omplayer_iframe' })

// 开发环境加载播放器
if (process.env.NODE_ENV !== 'production') {
  require('./index.js')
}

// 解析 querystring
const query = querystring.parse(location.search.slice(1))

Object.keys(query).forEach(key => {
  if (query[key] === 'true') query[key] = true
  if (query[key] === 'false') query[key] = false
})

query.omvid = query.omvid || 'RLo1mBoVpJ3uw218fLN1cNojtbddmcXlJnt1hOg8K6ZSJWl9HnUgNXRiAvi1rDwLFUuXDy4hVHBKPqPnrqnVK468kgQ_gnINEV1'

// 初始化视频播放器
let video = new Omplayer({ wrapElement: '#container', ...query })

video.on(`abort canplay canplaythrough durationchange emptied
  ended error loadeddata loadedmetadata loadstart mozaudioavailable
  pause play playing progress ratechange seeked seeking stalled
  suspend timeupdate waiting volumechange`, (e, data) => {
  printlog(e.type)

  // 忽略一些外部错误减少触发 sentry 告警
  if (e.type === 'error' && data && data.isIgnoreError) {
    e.stopPropagation()
  }
})

// video.on(`stalled`, e => {
//   printlog(e.type)
// })

// 初始化操作按钮
$('#play').on('click', e => {
  video.play()
})

$('#pause').on('click', e => {
  video.pause()
})

$('#seekTo10Play').on('click', e => {
  video.currentTimeAndPlay = 10
})

$('#seekTo10Pause').on('click', e => {
  video.currentTimeAndPause = 10
})

$('#playbackRate1').on('click', e => {
  video.playbackRate = 1
})

$('#playbackRate2').on('click', e => {
  video.playbackRate = 2
})

$('#replay').on('click', e => {
  video.replay()
})

$('#toFullScreen').on('click', e => {
  video.toFullScreen()
})

$('#destroy').on('click', e => {
  video.destroy()

  $('#controls input').attr({ disabled: 'disabled' })
})

$('#asyncplay').on('click', e => {
  setTimeout(() => {
    video.play()
  }, 2000)
})

$('#load').on('click', e => {
  video.destroy()
  delete query.vid;
  query.omvid = query.omvid || 'RLo1mBoVpJ3uw218fLN1cNojtbddmcXlJnt1hOg8K6ZSJWl9HnUgNXRiAvi1rDwLFUuXDy4hVHBKPqPnrqnVK468kgQ_gnINEV1'
  video = new Omplayer({ wrapElement: '#container', ...query })
  video.load()
})
$('#loadvid').on('click', e => {
  video.destroy()
  delete query.omvid;
  query.vid = query.vid || 'o09270q06xq'
  video = new Omplayer({ wrapElement: '#container', ...query })
  video.load()
})

// 初始化事件流水
const printlog = msg => {
  $('#logs tbody').prepend(`<tr><td>${video.currentTime}</td><td>${msg}</td></tr>`)
}
